.newUser {
  flex: 6;
  padding: 20px;
  margin: 20px;
  box-shadow: 0px 0px 15px 10px rgba(0, 0, 0, 0.352);
  border-radius: 10px;
  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    //align-items: center;
    padding: 20px;
    //align-items: center;

    .form-inputs {
      padding: 20px;
      width: 100%;
     // height: 600px;
      display: flex;
      flex-wrap: wrap;
      //border: 1px solid red;

      .input-item {
        width: 400px;
        display: flex;
        flex-direction: column;
        padding: 10px;
        & > label {
          margin-bottom: 10px;
        }
        & > input {
          width: 300px;
          height: 40px;
          border-radius: 10px;
          border: 1px solid gray;
          padding: 10px;
          //border-radius: 2px;
          // border: none;
          // border-bottom: 1px solid #55555552;
        }
        .select-item {
          padding: 10px;
          width: 300px;
          border-radius: 10px;
          border: 1px solid grey;
          background-color: white;
          cursor: pointer;
         
        }
        .item-gender {
          width: 300px;
          display: flex;
          align-items: center;
          justify-content: center;
          border-radius: 10px;
           
          border: 1px solid grey;
          &>input{
            cursor: pointer;
          }
          &>label{
            margin-right: 20px;
            cursor: pointer;
          }
        }
      }
      .item-gender {
        padding: 15px;
        & > label {
          margin-right: 10px;
        }
        & > input {
          margin-right: 5px;
        }
      }
    }
    button {
      width: 300px;
      margin: 30px 10px;
      height: 50px;
      border: none;
      background: darkblue;
      color: white;
      font-size: 22px;
      font-weight: 500;
      border-radius: 10px;
      cursor: pointer;
      &:hover{
        background: rgba(0, 0, 139, 0.904);
      }
    }
  }
}
